.videos-box {
  margin-top: 25px;
  position: absolute;
  width: 200px;
  height: 80px;
  overflow: hidden;
  border-radius: 5px;
  border: none;
  font-size: 1.5em;
  font-family: 'Spartan', sans-serif;
  font-weight: bold;
  padding-top: 1px;
  background-color: rgba(100, 33, 155, 0.849);
  color: rgb(228, 213, 163);
  cursor: pointer;
  border: solid rgba(156, 102, 228, 0.473) 3px;
  transition: 0.5s;
}

.videos-box i {
  color: rgb(180, 110, 245);
  font-size: 1.2em;
  padding: 5px;
  padding-top: 7px;
}

.videos-box:hover {
  background-color: rgb(159, 76, 238);
  transition: 0.5s;
}

.videos-box:before {
  position: absolute;
  top: 0;
  left: -500px;
  content: "";
  width: 120px;
  height: 500px;
  background: rgba(255, 255, 255, 0.6);
  -webkit-transform: skew(-50deg);
          transform: skew(-50deg);
  /* transition: 1s; */
  -webkit-animation: shine 3s ease infinite;
          animation: shine 3s ease infinite;
}

@-webkit-keyframes shine {
  from {left: -500px;}
  to {left: 655px;}
}

@keyframes shine {
  from {left: -500px;}
  to {left: 655px;}
}
