/* MINT */
.shine-box {
    margin-top: 25px;
    position: absolute;
    width: 200px;
    height: 80px;
    overflow: hidden;
    border-radius: 5px;
    border: none;
    font-size: 3em;
    font-family: 'Spartan', sans-serif;
    font-weight: bold;
    padding-top: 12px;
    background-color: rgba(0, 255, 221, 0.849);
    color: rgb(202, 244, 255);
    cursor: pointer;
    border: solid rgba(255, 255, 255, 0.363) 3px;
    transition: 0.5s;
  }

  .shine-box:hover {
    background-color: rgb(0, 247, 255);
    transition: 0.5s;
  }
  
  .shine-box:before {
    position: absolute;
    top: 0;
    left: -500px;
    content: "";
    width: 120px;
    height: 500px;
    background: rgba(255, 255, 255, 0.6);
    -webkit-transform: skew(-50deg);
            transform: skew(-50deg);
    /* transition: 1s; */
    -webkit-animation: shine 3s ease infinite;
            animation: shine 3s ease infinite;
  }
  
  @-webkit-keyframes shine {
    from {left: -500px;}
    to {left: 655px;}
  }
  
  @keyframes shine {
    from {left: -500px;}
    to {left: 655px;}
  }
